.container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80vw;
  max-width: 625px;
  border-radius: 50px;
  background-color: white;
  box-shadow: 3px 3px 5px gray;
  padding: {
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 10px;
  }
  margin: {
    top: 70px;
    left: auto;
    right: auto;
  }

  .meb-box {
    display: flex;
    width: 100%;
    .meb-box--user {
      display: flex;
      align-items: center;
      margin: {
        right: 15px;
      }
      .profile-img {
        border: 1px solid rgb(83, 83, 83);
        border-radius: 50%;
        width: 30px;
        height: 30px;
      }
      .username {
        font-weight: 700;
        margin: {
          left: 5px;
        }
      }
    }

    .meb-box__text {
      display: flex;
      align-items: center;
      flex-grow: 1;
      line-height: 20px;
      margin: {
        right: 15px;
      }
    }
    .meb-box__img {
      display: flex;
      justify-content: center;
      height: 80px;
      width: 80px;
      overflow: hidden;
      border: 1px solid rgb(204, 204, 204);
      border-radius: 50%;
      background-color: white;
      cursor: pointer;
      &:hover {
        filter: brightness(0.7);
      }
      img {
        height: 80px;
      }
    }
  }

  .btn--edit {
    right: 80px;
  }

  .btn--delete {
    right: 40px;
  }

  .btn {
    top: -35px;
    position: absolute;
    width: 30px;
    height: 30px;
    background-color: white;
    border-radius: 50%;
    border: 2px solid #221718;
    cursor: pointer;
    &:hover {
      color: rgb(255 53 87);
      border-color: rgb(255 53 87);
    }
  }
  .edit--text {
    outline: none;
    border-radius: 10px;
    width: 80vw;
    max-width: 400px;
    height: 50px;
    border: 1px solid #221718;
    font-size: 16px;
    line-height: 16px;
    padding: {
      top: 3px;
      left: 20px;
      right: 110px;
    }
    &:focus {
      border: 1px solid white;
      box-shadow: rgb(255 53 87) 0px 0px 21px;
    }
  }
  .edit--submit {
    cursor: pointer;
    background-color: rgb(122, 196, 137);
    color: whitesmoke;
    border: none;
    border-radius: 5px;
    height: 30px;
    font-weight: 700;
    font-size: 16px;
    padding: {
      left: 10px;
      right: 10px;
      top: 6px;
      bottom: 5px;
    }
    margin: {
      left: 10px;
      right: 10px;
    }
    &:hover {
      background-color: rgb(139, 224, 156);
    }
  }
  .edit--cancel {
    cursor: pointer;
    background-color: rgb(206, 93, 93);
    color: whitesmoke;
    border: none;
    border-radius: 5px;
    height: 30px;
    font-weight: 700;
    font-size: 16px;
    padding: {
      left: 10px;
      right: 10px;
      top: 6px;
      bottom: 5px;
    }
    &:hover {
      background-color: rgb(233, 133, 133);
    }
  }
}
